    font-size: 80% !important;
    overflow-x: hidden;
}

/* hacking for ABA (workaround for long location) */

.t5.t5-meetus-manual.administration-business-affairs.meet-us .contact-block {
    font-size: 80% !important;
    overflow-x: hidden;
}

.t5.t5-meetus-manual.administration-business-affairs.financial-services.meet-us .contact-block,
.t5.t5-meetus-manual.administration-business-affairs.business-administrative-services.meet-us .contact-block {
    font-size: 73% !important;
    overflow-x: hidden;
}

.t5.t5-meetus-manual.administration-business-affairs.facilities-management.meet-us .contact-block {
    font-size: 68% !important;
    overflow-x: hidden;
}

.t5.t5-meetus-manual section#flex-section-prepend {
    margin-bottom: 2em;
}

/***************************************** 
 .meeting-link style 
 *****************************************/

/* Copied from software catalog pills */

.t5.t5-meetus-manual ul.meeting-links li {
    background-color: #e8e4d4;
    color: #1d573d;
    padding: .4em 1em;
    padding-bottom: 0.4em;
    font-size: .8em;
    text-align: center;
    border-radius: 8px;
    width: min-content;
    flex-basis: content;
    margin-right: 0.4em;
    margin-top: 0.4em;
    display: inline-flex;
    cursor: pointer;
    flex: 1 0;
    white-space: nowrap;
    justify-content: center;
}

.t5.t5-meetus-manual ul.meeting-links li:hover {
    text-decoration: none;
}

